import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

import {
  createGallery,
  clearGallery,
  showLoader,
  hideLoader,
  showLoadMoreButton,
  hideLoadMoreButton,
} from './js/render-functions.js';

import { getImagesByQuery } from './js/pixabay-api.js';

const form = document.querySelector('.form');
const input = document.querySelector('[name="search-text"]');
const gallery = document.querySelector('.gallery');
const loader = document.querySelector('.loader');
const btnLoadMore = document.querySelector('.js-load-more');

let page = 1;
let query = '';
let totalShow = 0;

form.addEventListener('submit', handleSubmit);

btnLoadMore.addEventListener('click', handleClick);

function handleSubmit(event) {
  event.preventDefault();

  query = input.value.trim().toLowerCase(); // –í–∑—è—Ç–∏ —Ç–µ–∫—Å—Ç –∑ input —ñ –ø—Ä–∏–≤–µ—Å—Ç–∏ –¥–æ –Ω–∏–∂–Ω—å–æ–≥–æ —Ä–µ–≥—ñ—Å—Ç—Ä—É
  page = 1;
  totalShow = 0;

  if (!query) {
    iziToast.error({
      title: 'Error',
      message: 'Please enter a search term.',
      position: 'topRight',
    });
    return;
  }

  clearGallery(); // –û—á–∏—Å—Ç–∏—Ç–∏ —Å—Ç–∞—Ä—ñ –∫–∞—Ä—Ç–∏–Ω–∫–∏
  showLoader(); // –ü–æ–∫–∞–∑–∞—Ç–∏ —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è

  getImagesByQuery(query, page)
    .then(data => {
      if (data.hits.length === 0) {
        iziToast.error({
          title: '',
          message:
            'Sorry, there are no images matching your search query. Please try again!',
          position: 'topRight',
        });
        return;
      }

      createGallery(data.hits); // data.hits ‚Äî —Ü–µ –º–∞—Å–∏–≤ –∫–∞—Ä—Ç–∏–Ω–æ–∫
      totalShow += data.hits.length;

      if (totalShow >= data.totalHits) {
        hideLoadMoreButton();
      } else {
        showLoadMoreButton();
      }
    })

    .catch(error => {
      iziToast.error({
        title: 'Error',
        message: `Something went wrong: ${error.message}`,
        position: 'topRight',
      });
    })
    .finally(() => {
      hideLoader(); // –ü—Ä–∏—Ö–æ–≤–∞—Ç–∏ –ª–æ–∞–¥–µ—Ä —É –±—É–¥—å-—è–∫–æ–º—É –≤–∏–ø–∞–¥–∫—É
    });
}

async function handleClick() {
  page += 1;
  try {
    const dataGallery = await getImagesByQuery(query, page);
    console.log(dataGallery);

    createGallery(dataGallery.hits);

    const firstCard = document.querySelector('.gallery-item');

    if (firstCard) {
      const { height: cardHeight } = firstCard.getBoundingClientRect();

      window.scrollBy({
        top: cardHeight * 2,
        behavior: 'smooth',
      });
    }

    totalShow += dataGallery.hits.length;

    if (totalShow >= dataGallery.totalHits) {
      hideLoadMoreButton();
      iziToast.info({
        title: 'Info',
        message: `We're sorry, but you've reached the end of search results.`,
        position: 'topRight',
      });
    }
  } catch (error) {
    iziToast.error({
      title: 'Error',
      message: `Something went wrong: ${error.message}`,
      position: 'topRight',
    });
  }
}
